import React from 'react';
import { Typography, Toolbar, AppBar, IconButton } from '@material-ui/core';

const NavBar = props => {

    return (
        <div>
            <AppBar position="fixed" style={{ backgroundColor: '#121212' }} >
                <Toolbar>
                    <Typography style={{ textShadow: '2px 1px #686868', textAlign: 'left', width: '100%' }} align='left' variant="h6" color='inherit'>
                        Covid-19 Current Statistics
                    </Typography>
                </Toolbar>
            </AppBar>
        </div>
    )
}


export default NavBar
